// src/components/FileManager.js
import React, { useState, useEffect } from "react";
import axios from "axios";
import { toast } from "react-toastify";

const FileManager = () => {
  const [files, setFiles] = useState([]);
  const [selectedFile, setSelectedFile] = useState(null);

  //      
  useEffect(() => {
    const fetchFiles = async () => {
      try {
        const response = await axios.get("http://localhost:3001/files");
        setFiles(response.data);
      } catch (error) {
        toast.error("   : " + error.message);
      }
    };
    fetchFiles();
  }, []);

  //   
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file && file.size > 5 * 1024 * 1024) {
      toast.error("  !  : 5 .");
      return;
    }
    setSelectedFile(file);
  };

  //    
  const handleUpload = async () => {
    if (!selectedFile) {
      toast.error("   ");
      return;
    }

    try {
      const formData = new FormData();
      formData.append("file", selectedFile);

      //    
      const response = await axios.post("http://localhost:3001/upload", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });

      //       
      await axios.post("http://localhost:3001/files", {
        name: selectedFile.name,
        url: response.data.url,
      });

      //      
      setFiles((prevFiles) => [
        ...prevFiles,
        { id: Date.now(), name: selectedFile.name, url: response.data.url },
      ]);

      toast.success("  !");
      setSelectedFile(null);
    } catch (error) {
      toast.error("   : " + error.message);
    }
  };

  //  
  const handleDeleteFile = async (id, name) => {
    try {
      //    
      await axios.delete(`http://localhost:3001/delete-file/${name}`);

      //     
      await axios.delete(`http://localhost:3001/files/${id}`);

      //      
      setFiles((prevFiles) => prevFiles.filter((file) => file.id !== id));

      toast.success(" !");
    } catch (error) {
      toast.error("   : " + error.message);
    }
  };

  return (
    <div>
      <h2> </h2>

      {/*   */}
      <input type="file" onChange={handleFileChange} />
      <button onClick={handleUpload}> </button>

      {/*   */}
      <div>
        {files.length === 0 ? (
          <p>  .</p>
        ) : (
          files.map((file) => (
            <div key={file.id}>
              <a href={file.url} target="_blank" rel="noopener noreferrer">
                {file.name}
              </a>
              <button onClick={() => handleDeleteFile(file.id, file.name)}></button>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default FileManager;